body,
html {
    padding: 0;
    border-width: 0;
    height: calc(100% - 2em);
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

body {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f24dc6+0,207ce5+100 */
    background: #d74aac; /* Old browsers */
    background: -moz-linear-gradient(left, #d74aac 0%, #207ce5 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #d74aac 0%,#207ce5 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #d74aac 0%,#207ce5 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f24dc6', endColorstr='#207ce5',GradientType=1 ); /* IE6-9 */

    background-repeat: no-repeat;
    background-attachment: fixed;
}

/* menu */

aside {
    color: #fff;
    display: grid;
    grid-template-columns: auto 2em;
    padding: 2em;
}

img {
    width: 2.3em;
    cursor: pointer;
}

/* name */

figure {
    margin: 0;
}

figcaption {
    font-size: 1.3em;
    font-weight: bolder;
}

/* menu */

nav {
    background: #fff;
}

ul {
    display: none;
}

ul.active {
    display: block;
    position: absolute;
    background: #fff;
    height: 100%;
    width: 50%;
    left: 0;
    top: 0;
}

ul {
    margin-top: 0;
    padding-left: 0;
    list-style-type: none;
}

ul li a {
    color: #000;
    text-decoration: none;
    text-transform: uppercase;
    font-size: .8em;
    display: block;
    padding: 1.5em 3em;
    background-color: rgba(255, 255, 255, .35);
}

ul li a:hover {
    background-color: rgba(232, 232, 232, 0.35);
}

/* main */

main {
    padding: 2em;
}

h1 {
    margin: 0;
    font-size: 2em;
    font-weight: bolder;
}

p {
    font-size: 1.05em;
    line-height: 1.4em;
}

#action {
    color: white;
    background-color: dodgerblue;
    padding: 1.2em 3.6em;
    border-radius: .3em;
    border-color: black;
    border-width: 0;
    font-size: 1.2em;
    font-weight: bolder;
    text-decoration: none;
    display: block;
    margin: 3em auto;
    text-align: center;
}
